async function userRegistrySave(
  identification,
  name,
  lastname,
  email,
  password,
  password2,
  history,
  setIsLoading,
  setError,
  setAnswer
) {
  try {
    setError(false);
    setIsLoading(true);
    let response = await fetch(
      `${config.APIFireBase}/api/user`,
      {
        crossDomain: true,
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Methods": "DELETE, POST, GET, OPTIONS",
        },

        body: JSON.stringify({
          id: identification,
          name: name,
          lastname: lastname,
          email: email,
          password: password,
          fk_dependence: "0hSRJuu0Zrwq0w0euTyH",
        }),
      },
      { mode: "cors" }
    );
    let json = await response;
    type="Registro";
    responseApi(json, setIsLoading, setError, setAnswer, history, type);
  } catch (error) {
    setError(true);
    setAnswer(true);
    console.error(error);
  }
}

function responseApi(res, setIsLoading, setError, setAnswer, history, type) {
  console.log("esto es la respuesta", res.description, res.json());
  if (typeof res.error !== "undefined") {
    setIsLoading(false);
    setError(true);
    console.log("Upps!", res.error);
  } else {
    if (typeof res.status === "undefined" || res.status !== 200) {
      setIsLoading(false);
      setError(true);
      setAnswer(true);
      console.log(JSON.stringify(res));
      console.log("Upps!", "Algo pas, intenta de nuevo");
    } else {
      if (res.status == 200) {
        setError(null);
        setIsLoading(false);
        if(type=="Registro"){
          history.push("/");
        }else{
          if(type == "Bienes"){
            history.push("/app/dashboard")
          }
        }
        console.log("respuesta!!!!!!!!!", res);
      } else {
        setError(true);
        setIsLoading(false);
        setAnswer(true);
        console.log("Upps!", "Algo pas intenta de nuevo", res);
      }
    }
  }
}
async function saveGood(
  nameGood,
  description,
  idPlate,
  place,
  physical_space,
  brand_and_serie,
  state,
  reason,
  history,
  setIsLoading,
  setError,
  setAnswer
) {
  try {
    setError(false);
    setIsLoading(true);
    let response = await fetch(
      `${config.APIFireBase}/api/good`,
      {
        crossDomain: true,
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Methods": "DELETE, POST, GET, OPTIONS",
        },

        body: JSON.stringify({
          name: nameGood,
          description: description,
          idPlate: idPlate,
          place: place,
          fk_dependence: showDependence,
          physical_space: physical_space,
          brand_and_serie: brand_and_serie,
          state: state,
          reason: reason,
        }),
      },
      { mode: "cors" }
    );
    let json = await response;
    type="Bienes";
    responseApi(json, setIsLoading, setError, setAnswer, history, type);
  } catch (error) {
    setError(true);
    setAnswer(true);
    console.error(error);
  }
}

